import React, { Component } from 'react';
import '../App.css';

class NewStudent extends Component {
    state={
        password:"",
        name:"",
        age:"",
        course:"",
        img:""
    }
  render() {
    return (
      <div className="new"><br/><br/><br/>
      <h4>Insert new student to database</h4><br/>
        <input type='text' placeholder='Enter name' name='name' value={this.state.name} onChange={this.handleChange.bind(this)}></input><br/>
        <input type='text' placeholder='make a unique id' name='password' value={this.state.password} onChange={this.handleChange.bind(this)}></input><br/>
        <input type='number' placeholder='Enter age' name='age' value={this.state.age} onChange={this.handleChange.bind(this)}></input><br/>
        <input type='text' placeholder='Enter course id' name='course' value={this.state.course} onChange={this.handleChange.bind(this)}></input><br/><br/>
        <input type='text' placeholder='Insert pic url' name='img' value={this.state.img} onChange={this.handleChange.bind(this)}></input><br/><br/>
        <input type="submit" onClick={this.fetcher.bind(this)} value="Submit"></input>
      </div>
    );
  }
  handleChange(ev){
      this.setState({[ev.target.name]:ev.target.value})
  }

  async fetcher(){
      var res = await fetch('http://localhost:3000/newstudent', {
        method: 'POST',
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(this.state)
      });
      const content = await res.json();
  }
}

export default NewStudent;
