import React, { Component } from 'react';
import '../App.css';

class ViewCourse extends Component {
    state = {
        courseid: "",
        name: "",
        age: ""
    }
    render() {
        return (
            <div className="new"><br /><br /><br />
                <form method='post' action='\viewcourse'>
                    <h4>View a course from the database</h4><br />
                    <input type='text' placeholder='Enter course ID' name='courseid' value={this.state.courseid} onChange={this.handleChange.bind(this)}></input><br /><br />
                    {/* <input type='text' placeholder='Enter pet name' name='name' value={this.state.name} onChange={this.handleChange.bind(this)}></input><br/>
        <input type='text' placeholder='Enter pet age' name='age' value={this.state.age} onChange={this.handleChange.bind(this)}></input><br/><br/> */}
                    <input type="submit" value="view"></input>
                </form>
            </div>
        );
    }
    handleChange(ev) {
        this.setState({ [ev.target.name]: ev.target.value })
    }

    async fetcher() {
        var res = await fetch('http://localhost:3000/viewcourse', {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(this.state)
        });
        const content = await res.json();
    }
}

export default ViewCourse;
